(function () {
    'use strict';
    var angular = require('angular');

    var comp = angular.module('app').component('panMismatch', {
        template: require('./pan-mismatch.component.html'),
        controller: ['appState', 'analyticService', function (appState, analyticService) {
            var vm = this;
            vm.$onInit = function () {
                analyticService.trackPageLoad(analyticService.pageList.panMismatch);
                appState.clearAll();
            };
            vm.exit = function () {
                analyticService.buttonList.buttonClick.event_content = "PAN mismatch: Exit";
                analyticService.trackUserAction(analyticService.buttonList.buttonClick);
                window.location.href = "http://www.hsbc.co.in/";
            }
        }]
    });

    module.exports = comp.name;
})();



